import { Component, AfterViewInit } from '@angular/core';
import { UserService, Script } from './../../shared/services/index';
import { Datatable } from '../../shared/interfaces/datatable.interface';

declare var jQuery: any;

@Component({
	selector: 'og-email-logs',
	templateUrl: './email-logs.component.html',
	styleUrls: ['./email-logs.component.css', './../ionicons.min.css'],
})

export class EmailLogsComponent extends Datatable implements AfterViewInit {
	data: Object = [];
	loading: boolean = false;
	constructor(private _userService: UserService, private _script: Script) {
		super();
	}


	ngAfterViewInit() {
		this._script.load('datatables')
			.then((data) => {
				this.getEmails();
			})
			.catch((error) => {
				console.log('Script not loaded', error);
			});
	}

	getEmails() {
		this.loading = true;
		let obj = {
			limit: this.current_limit,
			page: this.current_page - 1,
			searchKey: this.search
		};
		this._userService.getEmailLogs(obj)
			.subscribe(
			(response: any) => {
				this.data = response.emails;
				this.total_pages = Math.ceil(response.count / this.current_limit);
				this.loading = false;
			},
			(response: any) => {
				console.log('Email error', response);
				this.loading = false;
			}
			);
	}


	paging(num: number) {
		super.paging(num);
		this.getEmails();
	}

	limitChange(event: any) {
		super.limitChange(event);
		this.getEmails();
	}

	previous() {
		super.previous();
		this.getEmails();
	}

	next() {
		super.next();
		this.getEmails();
	}

	searchData() {
		super.searchData();
		this.getEmails();
	}
}



