import { Component, OnInit } from '@angular/core';
import { Script } from './../../shared/services/index';
import { AdminService } from './../../shared/services/admin.service';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
declare var jQuery: any;

@Component({
  selector: 'og-sitesettings',
  templateUrl: './sitesettings.component.html',
  styleUrls: ['./sitesettings.component.css', './../ionicons.min.css','./../../../assets/css/sahil-hover.css','./../../../assets/css/custom-material.css'],
})

export class SitesettingsComponent implements OnInit {
  edit_appsumo_hellobar:boolean = false;
  appsumoMessage:any = null;
  loadingAppsumo:boolean = true;
  editAppsumoMessageForm:FormGroup;

  edit_trial_hellobar:boolean = false;
  trialMessage:any = null;
  loadingTrial:boolean = true;
  editTrialMessageForm:FormGroup;

  constructor(private _adminService: AdminService,
              private _script: Script,
              private fb : FormBuilder) {
  }
  ngOnInit() {
    this.getHellobarMessage();
    this.edit_appsumo_hellobar = false;
    this.editAppsumoMessageForm = this.fb.group({
      edit_appsumoMessage:this.appsumoMessage
    });
    this.edit_trial_hellobar = false;
    this.editTrialMessageForm = this.fb.group({
      edit_trialMessage:this.trialMessage
    });
  }

  getHellobarMessage(){
    let self = this;
    self.loadingAppsumo = true;
    self.loadingTrial = true;
    let getHellobarMessage = self._adminService.getSiteSettings()
      .subscribe(
        (success:any)=>{
          self.loadingTrial = false;
          self.loadingAppsumo = false;
          self.appsumoMessage=success.appsumoHelloBar;
          self.editAppsumoMessageForm.value.edit_appsumoMessage = success.appsumoHelloBar;

          self.trialMessage=success.trialHelloBar;
          self.editTrialMessageForm.value.edit_trialMessage = success.trialHelloBar;
        },(error:any)=>{
          console.log('getHellobarMessage',error);
          getHellobarMessage.unsubscribe();
          self.loadingTrial = false;
          self.loadingAppsumo = false;
        }
      );
  }
  editAppsumoMessage(){
    let self = this;
    self.loadingAppsumo = true;
    let editAppsumoMessage = self._adminService.editMessage(self.editAppsumoMessageForm.value,'appsumo')
      .subscribe(
        (success:any)=>{
          self.edit_appsumo_hellobar = false;
          self.loadingAppsumo = false;
          self.appsumoMessage=success.appsumoHelloBar;
          self.editAppsumoMessageForm.value.edit_appsumoMessage = success.appsumoHelloBar;
        },(error:any)=>{
          console.log('getHellobarMessage',error);
          editAppsumoMessage.unsubscribe();
          self.loadingAppsumo = false;
        }
      );
  }

  editTrialMessage(){
    let self = this;
    self.loadingTrial = true;
    let editTrialMessage = self._adminService.editMessage(self.editTrialMessageForm.value,'trial')
      .subscribe(
        (success:any)=>{
          self.edit_trial_hellobar = false;
          self.loadingTrial = false;
          self.trialMessage=success.appsumoHelloBar;
          self.editTrialMessageForm.value.edit_trialMessage = success.trialHelloBar;
        },(error:any)=>{
          console.log('getHellobarMessage',error);
          editTrialMessage.unsubscribe();
          self.loadingTrial = false;
        }
      );
  }
}



